.search-input {
  background-image: url("../../assets/search.png");
  height: 30px;
  background-position: 10px;
  background-repeat: no-repeat;
  padding-left: 40px;
  border-radius: 20px;
  border-color: #000;
  outline: none;
  margin-right: 10px;
}

.search-button {
  background-color: rgb(150, 31, 247);
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
}

.search-button:hover {
  background-color: rgb(181, 91, 255);
}
